.latestPost {
  color: #000;
  font-weight: 600;
  font-size: 32px;
  margin-top: 18px;
}
.filterCategory {
  padding-left: 8px;
  display: inline;
  font-size: 16px;
  font-weight: 600;
}
.items {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-bottom: 20px;
}
.content {
  padding: 0px 14px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 29px;
  color: #0084ff;
  background-color: #f5f5f5;
  width: auto;
  border: 1px solid #0084ff;
  border-radius: 15px;
  margin: 5px;
  cursor: pointer;
  font-size: 13px;
}
.content:hover {
  color: white;
  background-color: #0084ff;
}
